@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
body {
  margin: 0;
  max-width: 1200px;
  min-width: 320px;
}
@media (min-width: 1200px) {
  body {
    margin: 0 auto;
  }
}
img {
  max-width: 100%;
  object-fit: cover;
  z-index: 1;
}
a {
  text-decoration: none;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.header {
  width: 100%;
  min-height: 56px;
}
@media (min-width: 768px) {
  .header {
    position: sticky;
    top: 0;
    z-index: 100;
  }
}
.header__logo {
  position: absolute;
  z-index: 100;
  top: 0;
  left: calc(50% - 92px);
  width: 185px;
  height: 96px;
  margin: 0;
}
.header-nav {
  position: relative;
  min-width: 320px;
  min-height: 56px;
  background-color: #ffffff;
}
.header-nav--disablejs {
  position: relative;
  margin-top: 98px;
  width: 100%;
}
.header-nav__toggle {
  position: absolute;
  display: block;
  padding: 0;
  z-index: 200;
  top: 16px;
  right: 20px;
  width: 24px;
  height: 23px;
  border: none;
  background-color: transparent;
}
.header-nav__toggle--disablejs {
  display: none;
}
.header-nav__toggle:hover {
  opacity: 0.6;
}
.header-nav__toggle:active {
  opacity: 0.3;
}
.header-nav__toggle > span {
  display: block;
  height: 4px;
  width: 100%;
  padding: 0;
  top: 21px;
  background: #81b3d2;
  text-align: center;
  margin: 0;
  transition: 0.5s;
}
.header-nav__toggle > span::after {
  content: "";
  display: block;
  bottom: 0;
  right: 0;
  height: 4px;
  width: 100%;
  position: absolute;
  background: #81b3d2;
  transition: 0.4s;
}
.header-nav__toggle > span::before {
  content: " ";
  display: block;
  top: 0;
  right: 0;
  height: 4px;
  width: 100%;
  position: absolute;
  background: #81b3d2;
  transition: 0.4s;
}
.header-nav__toggle--open > span {
  width: 0;
  background-color: #ffffff;
  transition: 0.5s;
}
.header-nav__toggle--open > span::before {
  transform: rotate(45deg);
  width: 30px;
  top: 9px;
  background-color: #ffffff;
  transition: 0.4s;
}
.header-nav__toggle--open > span::after {
  transform: rotate(-45deg);
  width: 30px;
  bottom: 10px;
  background-color: #ffffff;
  transition: 0.4s;
}
.header-nav__wrapper {
  display: flex;
  flex-direction: column;
  z-index: 150;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  transition: all 0.4s ease;
}
.header-nav__wrapper--closed {
  left: -100%;
  transition: all 0.4s ease;
}
.header-nav__list {
  list-style-type: none;
  text-align: center;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background-color: #81b3d2;
}
.header-nav__list::after {
  content: "";
  position: absolute;
  bottom: 0;
  z-index: 100;
  width: 100%;
  height: 7px;
  mix-blend-mode: normal;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}
.header-nav__item a {
  text-decoration: none;
  color: #ffffff;
  width: 100%;
  padding: 0 10px;
}
.header-nav__item {
  position: relative;
  display: flex;
  width: auto;
  min-height: 55px;
  mix-blend-mode: normal;
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
  align-items: center;
  justify-content: center;
  margin: 0.5px;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  text-align: center;
}
.header-nav__item:hover {
  background-color: #669ec0;
}
.header-nav__item:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .header-nav {
    display: block;
    min-width: 640px;
  }
  .header-nav--disablejs {
    margin-top: 0;
  }
  .header-nav__toggle {
    display: none;
  }
  .header-nav__toggle--open {
    display: none;
  }
  .header-nav__wrapper {
    flex-direction: row;
    justify-content: space-between;
    z-index: 50;
  }
  .header-nav__wrapper--closed {
    display: flex;
    left: 0;
  }
  .header-nav__list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    justify-content: space-between;
    width: 100%;
    background-color: #ffffff;
  }
  .header-nav__list::after {
    display: none;
  }
  .header-nav__item {
    min-width: 153px;
    box-shadow: none;
    font-size: 14px;
  }
  .header-nav__item:nth-child(4n+3) {
    grid-column: 4/5;
  }
  .header-nav__item:hover {
    background-color: #ffffff;
  }
  .header-nav__item:active {
    opacity: 1;
  }
  .header-nav__item a {
    color: #000000;
  }
  .header-nav__item:hover a {
    color: #81b3d2;
  }
  .header-nav__item:active a {
    color: #000000;
    opacity: 0.3;
  }
  .header-nav__item--current:hover a {
    color: #766357;
  }
  .header-nav__item--current a {
    color: #766357;
  }
  .header-nav__item--current a:hover {
    color: #766357;
  }
}
@media (min-width: 1200px) {
  .header-nav {
    max-width: 1200px;
    margin: 0 auto;
  }
}
.hero {
  position: relative;
  display: flex;
  justify-content: center;
  min-height: 158px;
  background-color: #5e83ac;
