    background-size: 100% auto;
  }
  .hero::after {
    height: 61px;
    clip-path: polygon(0 100%, 0 0, 21.2% 42%, 21.2% 0, 50% 97%, 78.8% 0, 78.8% 42%, 100% 0, 100% 100%);
  }
  .hero--index {
