  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.checkbox__input:disabled + .checkbox__icon > .checkbox__tick {
  fill: #000000;
  opacity: 0.3;
}
.checkbox__tick {
  fill: none;
  margin-left: 12px;
  margin-top: 14px;
}
.checkbox__icon {
  display: block;
