  height: 46px;
  background-color: #f2f2f2;
  border-radius: 4px;
}
.checkbox__icon:hover {
  background-color: #e5e5e5;
}
.checkbox__icon:active > .checkbox__tick {
  opacity: 0.3;
}
.checkbox__input:checked + .checkbox__icon > .checkbox__tick {
  fill: #333333;
}
.checkbox__input:focus + .checkbox__icon {
  outline: auto;
}
